.songPage {
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
  background-color: #1E1D1D;
  height: 100vh;
  width: 100vw;
}

.songPage__song {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.songPage__Info {
  width: 50%;
  font-family: "Righteous", cursive;
  font-size: 2rem;
  font-weight: 300;
  color: white;
  font-size: 20px;
  padding-top: 30px;
}

.songPage__rightBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.songPage__label {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.songPage__control {
  width: 50%;
  background-color: #131010;
  position: relative;
  top: 2rem;
}

.lyricButton {
  width: 100px;
  height: 20px;
  background: #f3f0f1;
  position: relative;
  background: #f3f0f1;
  margin-bottom: 25px;
  border-radius: 32px;
  text-align: center;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.lyricButton span {
  font-family: "Righteous", cursive;
}

.relatedSongHeader {
  font-family: "Righteous", cursive;
  font-size: 2rem;
  font-weight: 300;
  color: white;
  text-align: center;
}
